// primary colors tinte e ombre------------------
$colorlist : 
	$primary-a1
	$primary-a2
	$primary-a3
	$primary-a4
	$primary-a5
	$primary-a6
	$primary-a7
	$primary-a8
	$primary-a9
	$primary-a10
	$primary-a11
	$primary-a12
	$primary-a13; 

@for $i from 1 through length($colorlist){
	$color: nth($colorlist, $i);
	.primary-bg-a#{$i}{
		background-color:$color; 
	}
	.primary-color-a#{$i}{
		color:$color; 
	}
	.primary-border-color-a#{$i}{
		border-color:$color; 
	}
}
